import { Component, OnInit } from '@angular/core';
import { PageEvent, MatTableDataSource } from '@angular/material';
import { StoreService } from 'src/app/services/sotre.service';
import { NotificationService } from 'src/app/@core/services/notification.service';
import { StringConst } from 'src/app/@core/utils/string-constants';

@Component({
  selector: 'app-stores',
  templateUrl: './stores.component.html',
  styleUrls: ['./stores.component.scss']
})
export class StoresComponent implements OnInit {

  public totalCount:number=0;
  public limit:number=5;
  public page:number=0;  
  public pageEvent: PageEvent;
  displayedColumns: string[] = ['name', 'address',  'phone_no' , 'email', 'imei_no', 'action'   ];
  public templateData: any;
  public add: string;
  public view: string;
  public delete: string;
  public edit: string;
  public isLoading: boolean = true;
  public checked: boolean = true;
  constructor(private storeService: StoreService, private notificationService: NotificationService) { }

  ngOnInit() {
    this.getStoreList();
     
    
  }

  getStoreList() {
    const request_obj = {
      page: 0,
      limit: 10,
      search_string: ''
    };
    this.storeService.listStore(request_obj).subscribe((res) => {
      console.log(res);
      this.isLoading = false;
      this.templateData = new MatTableDataSource(res['data']);      
      this.totalCount = res.total;
    }, () => {
      this.notificationService.infoToast(StringConst.SOMETHING_WENT_WRONG + ' ' + StringConst.PLEASE_TRY_AGAIN_LATER);
    });
  }



  
  pageChange(event){
    console.log("pagination",event);
    this.page = event.pageIndex;
    this.limit = event.pageSize;
    this.isLoading = true;
    this.getStoreList();
    }


}